.TabComponent{
   
   
    
    width: 50%;
   margin:10% 0 0 25%;
 
}
.Tabs{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: auto;
    margin:auto ;
}
.tab{
    width: 350px;
    height:30px;
    border: 1px solid #fff;
    border-bottom: none;
    border-radius: 3px;
    margin:auto ;
    cursor: pointer;
    text-align: center;
    padding-top: 10px;
    text-transform: uppercase;
    background-color: transparent;
}
.tab:hover{
background:-webkit-linear-gradient(#be5712,transparent) ;
background:linear-gradient(#be5712,transparent) ;
}
.Content{
  
    width: 100%;
    
    height: 350px;
    
    

}
.tab-content{
    display: none;
    margin: 2% auto 0 auto;
    
    text-align: center;
    height: 500px;
    width: 900px;
  
    background-color:rgba(255, 255, 255, 0.233);
    font-family: 'Kanit', sans-serif;
    position: relative;
    line-height: 1.5em;
    word-spacing: 0.1em;

}
p{
 padding: 50px;

}
a{
    color: #fff;
}
a:hover{
    color: #be5712;
}
@media only screen and (max-width: 767px) {
    .tab-content{
        height: 500px;
        width: 300px;
        line-height: 1em;
    }
    .tab{
        width: 150px; 
        margin-left:10px;
    }
    .TabComponent{
       
   margin:20% 0 0 25%;
    }
}
@media only screen and (max-width: 1023px) {
    .tab-content{
        height: 500px;
        width: 700px;
        line-height: 1em;
    }
    .tab{
         
        margin-left:10px;
    }
    .TabComponent{
       
        margin:20% 0 0 25%;
         }
}
.main-div{
    width:auto;
    height: 150vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-image: -webkit-linear-gradient(rgba(73, 71, 71, 0.192),rgba(73, 71, 71, 0.192)), url(/User-profile/static/media/background.78b7986f.jpg);
    background-image: linear-gradient(rgba(73, 71, 71, 0.192),rgba(73, 71, 71, 0.192)), url(/User-profile/static/media/background.78b7986f.jpg);
    background-position: center;
    background-size: cover;
    background-repeat:no-repeat ;
    color: #fff;
    font-size: 22px;
    
}
.header{
    height: 10%;
    width: auto;
    background-color:transparent;
    
}
img{
    height: 150px;
    width:150px;
    border-radius: 80px;
    margin: 5% 0 0 45%;
}
@media only screen and (max-width: 767px) {
    img{
        margin: 5% 0 0 40%;
    }
}
@media only screen and (max-width: 1023px) {
    img{
        margin: 5% 0 0 40%;
    }
}
* {
  margin: 0;
  padding: 0; }

